.login {
    background-color: white;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    height: 630px;
}

.mainLogo {
    position: relative; 
    font-family: Inter;
    font-size: 24px;
    font-weight: 700;
    line-height: 29px;
    letter-spacing: 0em;
    text-align: left;
    
    margin: 0 ;

    width: 228px;
    height: 62px;
    top: 157px;
    left: 36px;
}

.underLine {
    border : 3px black solid;
    width : 342px;
    position : relative; 
    top : 177px;
    left : 25px;
    margin: 0 auto;
}

.loginForm{
    display: flex;
    flex-direction: column;
}

.loginForm > button {
    position :relative;
    width: 253px ;
    height: 51px ;
    top: 357px ;
    left: 70px ;
    border-radius: 20px ;
    background: #1C4B76CC;
    border: none;
    font-family: Inter;
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: center;
    color: #F5F5F5CC;
}

.loginForm > input {
    width: 251px;
    height: 32px;
    top: 297px;
    left: 71px;
    border: none;
    border-bottom : 1px solid #353637;    
    position: relative;
}

.loginForm > .loginID {
    margin-bottom: 28px;
}

.loginForm > .signUpBtn {
    margin-top: 23px;;
    background: #CED7DECC;
    color : #000;
}