/*
view.js <-- 지홍이는 이 페이지를 보고 css를 추가하면 됩니다.

Hint!
. <- className 선택자
# <- id 선택자
A > B <- 부모가 A인 B요소만 선택함 
table, tbody, th, tr, td등을 사용을 했으니 이 태그들을 잘 사용하면 되겠죠?
모르는게 있으면 언제든 물어봐요~ hello world
*/
.viewTable > #writeDate {
    position : relative;
    top : 30px;
    left : -57px;
}
.viewResult table{
    margin: 20px;
    width: 360px;
    border-collapse: collapse;
    font-size: 15px;
    text-align: left;
    height: 520px;
    table-layout: fixed;
}
.viewResult td{
    background: #F5F5F5;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 1px;
    padding: 10px;
    text-align: center;
    width: 90px;
   table-layout: fixed;
    font-size: 15px;
}
.viewResult th{
    background: #F5F5F5;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 1px;
    padding: 10px;
    text-align: left;
    width: 90px;
   table-layout: fixed;
    font-size: 15px;
}
.checkList input, .checkList select{
    margin: 10px 5px;
}

